html {
  font-size: 52.08333333px;
  min-width: 1000px;
}
body {
  margin: 0;
  color: #040809;
  background-color: #fff;
}
body img {
  width: 100%;
}
body .app {
  padding: 0.2rem;
}
body .app p {
  margin-bottom: 0;
}
body .app-title {
  text-align: center;
  margin: 0.5rem 0;
}
body .app-title .main {
  font-size: 0.2rem;
  font-weight: bold;
}
body .app-title .sub {
  margin: 0.1rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
body .app-title .sub:before,
body .app-title .sub:after {
  content: '';
  display: inline-block;
  border-radius: 50%;
  background-color: #040809;
  margin: 0 0.2rem;
  padding: 0.04rem;
}
body .app-title .desc {
  border: 1px solid rgba(4, 8, 9, 0.5);
  display: inline-block;
  padding: 0.02rem 0.8rem;
}
body .app-logo {
  font-size: 0.2rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #040809;
}
body .app .icon {
  width: 1.6em;
  height: 1.6em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  cursor: pointer;
  display: inline-block;
}
body .app-fixed {
  position: fixed;
  left: 0.2rem;
  top: 40%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
body .app-fixed .icon {
  display: block;
}
body .app-fixed .icon:not(:first-child) {
  margin-top: 0.1rem;
}
body .app-fixed .icon:first-child .icon {
  color: #FF9999;
}
body .app-fixed .icon:nth-child(2) .icon {
  color: #FFCC33;
}
body .app-fixed .icon:nth-child(3) .icon {
  color: #99CC99;
}
body .app-fixed .icon:nth-child(4) .icon {
  color: #99CCFF;
}
body .app-back {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.5rem;
  text-align: center;
  cursor: pointer;
}
